*,*:after,*:before{-webkit-box-sizing:border-box;-moz-box-sizing:border-box;box-sizing:border-box}body,button,dd,dl,dt,fieldset,figcaption,figure,form,h1,h2,h3,h4,h5,h6,input,legend,li,ol,p,select,table,td,textarea,th,ul{margin:0;padding:0}body,button,input,select,table,textarea{font-family:"Apple SD Gothic Neo","Malgun Gothic","맑은 고딕",helvetica,sans-serif}a,a:focus,a:hover{color:inherit;text-decoration:none}h1,h2,h3,h4,h5,h6{font-weight:normal}ul{list-style:none}address,em{font-style:normal}strong{font-weight:normal}img{vertical-align:top;width:100%}button,input,select,textarea{-webkit-appearance:none;-moz-appearance:none;appearance:none;border-radius:0;font-size:16px;border:0}#skip a{position:absolute;left:10px;top:-92px;z-index:100000;font-size:1rem;padding:10px 30px;color:var(--white);background:var(--black)}#skip a:active,#skip a:focus{top:10px}.blind{position:absolute;width:1px;height:1px;padding:0;margin:-1px;overflow:hidden;clip:rect(0, 0, 0, 0);white-space:nowrap;border:0}.ir{display:block;overflow:hidden;font-size:0;line-height:0;text-indent:-9999px;white-space:nowrap}