@use './vars.scss' as var;

@mixin li_before{
        content: 'PAGE MOVE';
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        z-index: 10;
        display: flex;
        justify-content: center;
        align-items: center; 
        opacity: 0;
        color: white;
        transition: all 0.2s ease-in-out;
}

#contents {
    color: var.$con-font-color;
    width: 100%;
    margin-top: 80px;

    .inner {
        .contents_title{
            text-shadow: 0 0 5px #ccc;
        }
        width: 70%;
        margin: 0 auto;

        .arrival_list {
            margin-top: 10px;
            display: flex;
            justify-content: space-between;

            li {
                width: 30%;
                a {
                    img{
                        transition: all 0.2s ease-in-out;
                        box-shadow: 1px 1px 5px rgb(99, 99, 99);
                    }

                    p {
                        padding-top: 20px;
                    }
                }

                &:hover img{
                    opacity: 0.7;
                }
            }

        }

        #instagram {
            margin-top: 100px;

            .instagram_list {
                margin-top: 10px;
                display: flex;
                justify-content: space-between;

                li{
                    width: 23%;
                    overflow: hidden;
                    position: relative;
                    box-shadow: 1px 1px 5px rgb(99, 99, 99);
                }
                
                li:before{
                    @include li_before;
                }

                li:hover::before{
                    background-color: rgba(0, 0, 0, 0.658);
                    opacity: 1;
                }

                img{
                    transition: all 0.2s ease-in-out;
                }

                li:hover img{
                    transform: scale(1.05);
                }


            }

        }

        #products {
            margin-top: 100px;

            .Products_list{
                margin-top: 10px;
                display: flex;
                justify-content: space-between;

                li{
                    position: relative;
                    width: 33%;
                    overflow: hidden;
                    box-shadow: 1px 1px 5px rgb(99, 99, 99);
                }
                li::before{
                    @include li_before;
                }

                li:hover::before{
                    background-color: rgba(0, 0, 0, 0.658);
                    opacity: 1;
                }

                img{
                    transition: all 0.2s ease-in-out;
                }

                li:hover img{
                    transform: scale(1.05);
                }

            }
        }
    }

    #campain{
        width: 90%;
        margin-top: 500px;
        position: relative;
        margin: 0 auto;

        video{
            margin-top: 100px;
            width: 100%;
        }

        .news-card{
            display: flex;
            justify-content: center;
            margin-top: 50px;

            iframe{
                width: 50%;
                height: 400px;
            }

            .news_list{
                width: 35%;
     
                li{
                    margin: 30px 30px;
                    padding-bottom: 5px;
                    display: flex;
                    justify-content: space-between;
                    border-bottom: 1px solid #ccc;
                }

                li:nth-child(1){
                    margin: 0 30px;
                    text-align: center;
                    border-bottom: none;
                }

                p{
                    margin-right: 10px;
                }
            }
            
        }

    }

}


