#visual {
    width: 100%;
    position: relative;
    .swiper {
        margin-top: 40px;
        width: 100%;
        height: 95vh;
        position: absolute;
        left: 0;
        top: 0;

        .swiper-button-prev:after, .swiper-button-next:after{
            color: rgb(44, 44, 44);
        }

        .swiper-pagination-bullet-active{
            background: rgb(44, 44, 44);
        }
        .swiper-wrapper {

            .swiper-slide {

                img{
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                }
            }
        }

    }

    .inner {
        width: 70%;
        height: 100vh;
        margin: 0 auto;
        position: relative;
        z-index: 100;

        .visual_slogan {
            max-width: 50rem;
            color: white;
            position: absolute;
            left: 0%;
            top: 55%;
            text-shadow: 2px 2px 10px black;
            h2 {
                font-size: 6.25rem;
                span {
                    font-weight: 700;
                }
            }

            p {
                font-size: 20px;

            }

            .visual_btn {
                max-width: 39rem;
                display: flex;
                justify-content: space-between;
                flex-wrap: wrap;
                margin-top: 20px;
    
                .visual_btn_item {
                    width: 18.75rem;
                    height: 60px;
                    cursor: pointer;
                    box-shadow: 0 5px 10px black;
                    border-radius: 3px;
                    transition: all 0.2s ease-in-out;
                    margin-top: 5px;
                    &:hover{
                        background-color: rgba(0, 0, 0, 0.7);
                        color: white;
                    }
                }
    
            }
        }

    }
}


