@use './vars.scss' as var;

header{
    width: 100%;
    background-color: var.$black;
    position: fixed;
    z-index: 1000;
    color: var.$font-color;

    .inner{
        width: 70%;
        margin: 0 auto;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .close_btn{
        display: none;
    }

    .sub_gnb{
        padding: 10px 0 2px 0;
        display: flex;
        justify-content: end;
        gap: 15px;

        .material-symbols-outlined{
            font-size: 20px;
        }
    }

    .main_gnb{
        padding: 2px 0 10px 0;
        display: flex;
        gap: 40px;
        text-transform: uppercase;

        li{
            transition: all 0.3s ease-in-out;
            &:hover{
                color: white;
            }
        }
    }
}


@media (max-width:800px){
    header{
        width: 100%;
        background-color: var.$black;
        position: fixed;
        z-index: 1000;
        color: var.$font-color;
        padding: 10px 0;
    
        .inner{
            width: 90%;
            margin: 0 auto;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            align-items: flex-start;



            .gnb{
                width: 100%;
                display: none;
            }

            .action_gnb{
                display: block;
            }

            .sub_gnb{
                padding: 10px 0 2px 0;
                display: flex;
                align-items: center;
                justify-content: center;
                gap: 15px;
    
                .material-symbols-outlined{
                    font-size: 20px;
                }
            }
        
            .main_gnb{
                padding: 2px 0 10px 0;
                width: 100%;
                display: flex;
                align-items: center;
                flex-direction: column;
                gap: 0px;
                text-transform: uppercase;
        
                li{
                    transition: all 0.3s ease-in-out;
                    width: 100%;
                    text-align: center;
                    padding: 20px;

                    a{
                        display: inline-block;
                        width: 100%;
                        height: 100%;
                    }
                    &:hover{
                        color: white;
                    }
                }
            }
        }

        .close_btn{
            display: block;
            position: absolute;
            right: 1rem;
            top: 1rem;
            cursor: pointer;

            span{
            font-size: 2rem;
            }
        }

    }
}