@use './vars.scss' as var;

#footer{
    margin-top: 100px;
    width: 100%;
    background-color: #222222;

    color : var.$font-color;

    .inner{
        padding-top: 50px;
        width: 70%;
        margin: 0 auto;

        .footer_navi{
            text-align: center;
            display: flex;
            justify-content: space-between;

            li{
                width: 100%;
                ul{

                    li{
                        margin: 15px 0;
                    }
                }

            }
        }

        .sns_box > .sns_itemse{
            margin-top: 50px;
            display: flex;
            justify-content: center;
            gap: 50px;
        }

        .copyright{
            text-align: center;
            margin-top: 50px;
            padding-bottom: 20px;
        }

    }
}


@media (max-width: 800px) {
    #footer{
        margin-top: 50px;
        width: 100%;
        background-color: #222222;
    
        color : var.$font-color;
    
        .inner{
            padding-top: 50px;
            width: 80%;
            margin: 0 auto;
    
            .footer_navi{
                text-align: center;
                display: flex;
                flex-wrap: wrap;
                justify-content: center;
    
                li{
                    
                    width: 40%;
                    ul{

                        li{
                            margin: 15px 0;
                        }
                    }
    
                }
            }
    
            .sns_box > .sns_itemse{
                margin-top: 50px;
                display: flex;
                justify-content: center;
                gap: 20px;
            }
    
            .copyright{
                text-align: center;
                margin-top: 50px;
                padding-bottom: 20px;
            }
    
        }
    }
    
}